<template>
  <div class="card center product">
    <img :src="src" alt="Изображение товара" />
    <h2 class="text-center">{{ product.name }}</h2>
    <p class="product-price">Цена: <strong>{{ product.price }}&nbsp;&#8381;</strong></p>
    <button @click="addCart" class="button primary">Купить</button>
  </div>
</template>

<script>
import Image from '@/assets/logo.png';

export default {
  props: {
    product: Object,
  },
  inject: ['cart'],

  data() {
    return {
      src: Image,
    };
  },

  methods: {
    addCart() {
      if (this.cart) {
        const productDublicate = this.cart.filter((prod) => prod.id === this.product.id);
        if (productDublicate[0]) {
          return;
        }
        this.cart.push(this.product);
      } else {
        this.cart.push(this.product);
      }
    },
  },
};
</script>

<style scoped>
.product {
  width: 300px;
  margin: 10px;
  min-width: 280px;
}
.product-price {
  margin-top: 0;
}
</style>
