<template>
  <div class="cards">
  <div
    class="front card"
    :style="{
      backgroundColor: cardParams.formBackgroundColor,
      color: cardParams.formTextColor,
      borderColor: cardParams.formBorderColor,
    }"
  >
    <img v-if="cardParams.bankLogoSrc" :src="cardParams.bankLogoSrc" class="bank-logo">
    <img v-if="cardParams.brandLogoSrc" :src="cardParams.brandLogoSrc" class="brand-logo">
    <div class="fields">
      <input
        @input="fetchData"
        v-model.trim="number"
        class="field number"
        type="text"
        placeholder="0000 0000 0000 0000"
      >
      <input
        v-model.trim="cardHolder"
        class="field"
        type="text"
        placeholder="Ivanov Ivan"
      >
      <input
        v-model.trim="month"
        class="field expired"
        type="text"
        placeholder="MM"
      >
      <input
        v-model.trim="year"
        class="field expired"
        type="text"
        placeholder="YY"
      >
    </div>
  </div>
  <div class="back card">
    <input
      v-model.trim="password"
      class="field code"
      type="password"
    >
    <label class="label">Safety code</label>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      number: '',
      cardHolder: '',
      month: '',
      year: '',
      password: '',
      cardParams: {},
    };
  },

  methods: {
    async fetchData() {
      const apiKey = '0d0742913e0dbabec8952cb978f12727';
      const input = this.number;
      const url = `https://api.cardinfo.online?input=${input}&apiKey=${apiKey}`;

      const response = await fetch(url);
      const data = await response.json();

      const cardParams = {
        formBackgroundColor: data.formBackgroundColor,
        formTextColor: data.formTextColor,
        formBorderColor: data.formBorderColor,
        bankLogoSrc: data.formBankLogoBigSvg,
        brandLogoSrc: data.formBrandLogoSvg,
      };

      this.cardParams = cardParams;
    },
  },

};
</script>

