<template>
  <div class="cart">
    <section class="card center">
      <h2 class="text-center cart-title">Платежные реквизиты</h2>
      <payment-card></payment-card>
    </section>

    <section class="card">
      <h2 class="text-center cart-title">Список товаров в корзине</h2>
      <ul v-if="localCart.length > 0" class="list cart-list">
        <cart-item
          v-for="(item, idx) in localCart"
          :key="item"
          :idx="idx"
          :itemId="item.id"
          :cartItem="item"
          @remove-pruduct="removeProduct"
          @up-quantity="changeQuantity"
          @down-quantity="changeQuantity"
        ></cart-item>
      </ul>
      <div class="cart-flex cart-result">
        <h3 class="cart-summ">Сумма: {{ summ }} &nbsp;&#8381;</h3>
        <button class="button primary cart-buy">Купить</button>
      </div>
    </section>
  </div>
</template>

<script>
import PaymentCard from '../components/PaymentCard.vue';
import CartItem from '../components/CartItem.vue';

export default {
  components: { PaymentCard, CartItem },
  inject: ['cart'],
  data() {
    return {
      localCart: this.cart,
      changedCart: [],
      summ: 0,
    };
  },

  methods: {
    removeProduct(idx) {
      this.localCart[idx].quantity = 1;
      this.localCart.splice(idx, 1);

      this.calcSumm();
    },

    changeQuantity(productId, type) {
      const changedCart = [...this.localCart];
      if (type === 'up') {
        changedCart.forEach((prod) => {
          if (prod.id === productId) {
            // eslint-disable-next-line no-param-reassign
            prod.quantity += 1;
          }
          return prod;
        });
      }
      if (type === 'down') {
        changedCart.forEach((prod) => {
          if (prod.id === productId) {
            // eslint-disable-next-line no-param-reassign
            prod.quantity -= changedCart;
          }
          return prod;
        });
      }

      this.localCart = changedCart;

      this.calcSumm();
    },

    calcSumm() {
      let sum = 0;
      this.localCart.forEach((prod) => {
        sum += prod.quantity * +prod.price;
      });

      this.summ = sum;
    },
  },

  created() {
    this.calcSumm();
  },

};
</script>

