.cart {
  padding-top: 100px;
}

.cart .card {
  min-width: 845px;
}

.cart .cart-title {
  margin-bottom: 50px;
}

.cart .cart-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart .cart-list {
  margin-bottom: 70px;
}

.cart .cart-result {
  font-size: 30px;
  border-radius: 10px;
}

.cart-result .cart-buy {
  font-size: 30px;
}
</style>
