<template>
  <li class="list-item cart-item">
    <h2 class="cart-name">{{ cartItem.name }}</h2>
    <div class="cart-flex">
      <div class="cart-flex cart-wrapper">
        <button @click="downQuantity(itemId, 'down')" class="button warning">-</button>
        <p class="cart-count">Кол-во товара: {{ localQuantity }}</p>
        <button @click="upQuantity(itemId, 'up')" class="button warning">+</button>
      </div>
      <h3 class="cart-price">Цена: {{ cartItem.price }} &nbsp;&#8381;</h3>
      <button @click="removeProduct(idx, itemId)" class="button danger">
        Удалить
      </button>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    idx: Number,
    itemId: Number,
    cartItem: Object,
  },

  emits: {
    'remove-pruduct': null,
    'up-quantity': null,
    'down-quantity': null,
  },

  data() {
    return {
      localQuantity: this.cartItem.quantity,
    };
  },

  methods: {
    upQuantity(productId, type) {
      this.$emit('up-quantity', productId, type);
      if (this.localQuantity < 9999) {
        this.localQuantity += 1;
      }
    },

    downQuantity(productId, type) {
      this.$emit('down-quantity', productId, type);
      if (this.localQuantity > 1) {
        this.localQuantity -= 1;
      }
    },

    removeProduct(idx, itemId) {
      this.$emit('remove-pruduct', idx, itemId);
    },
  },
};
</script>

